import { getHttp, postHttp, putHttp } from "@/utils/http.js";

// 获取商品列表
let getGoodsList = (query,pagenum,pagesize) => getHttp(`goods?query=${query}&pagenum=${pagenum}&pagesize=${pagesize}`);
// 获取商品分类列表
let goodsCateList = ()=> getHttp(`categories`);
// 获取分类参数;
let getCateParams = (id,sel)=> getHttp(`categories/${id}/attributes?sel=${sel}`);
// 上传商品图片
let uoloadGoodsImg = (data)=> postHttp(`upload`,data);

// 添加商品
let addGoods = data =>postHttp(`goods`,data);
// 编辑参数属性
let updataParams = (id,attrId,data)=> putHttp(`categories/${id}/attributes/${attrId}`,data);
// 数据统计
let getData = ()=> getHttp('reports/type/1');
export {
    getGoodsList,
    goodsCateList,
    getCateParams,
    uoloadGoodsImg,
    addGoods,
    updataParams,
    getData
}